.container {
    text-align: center;
    height: auto;
    margin: 0;
    display: flex;
    justify-content: center;
    align-items: center;
    padding-left: 22vh;
    padding-top: 22vh;
}

.icon-container {
    color: rgb(51, 51, 51);
}

.icon-container p {
    color: rgb(51, 51, 51);
    font-size: 0.8rem;
}

.icon-row {
    display: flex;
    justify-content: center;
    grid-gap: 10px;
    gap: 10px;
}

.icon-link {
    display: inline-block;
    cursor: pointer;
}

.icon {
    width: 200px; 
    height: 200px; 
    transition: transform 0.2s;
}

.icon:hover {
    transform: scale(1.1);
}


.navbar {
    position: fixed;
    top: 0;
    width: 98%;
    background-color: white;
    border-bottom: 0.5px solid grey; /* Border at the bottom */
    padding: 15px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    transition: box-shadow 0.3s; /* Smooth transition for shadow */
    z-index: 100; /* Ensure navbar is above other content */
}

.navbar-brand {
    font-size: 24px;
}

.navbar-brand a:hover {
    text-decoration: none;
    color: #000000;
    font-size: 24px;
    font-weight: bold;
    border-radius: 100px;
    background-color: rgb(255, 251, 209);
}

.navbar-links {
    list-style: none;
    display: flex;
    grid-gap: 30px;
    gap: 30px;
    margin: 0;
    padding: 0;
}

.navbar-links li a {
    text-decoration: none;
    color: #333;
    font-size: 18px;
}

.navbar-links li a:hover {
    /* padding: 10px; */
    border-radius: 100px;
    /* color: #f9f9f9; */
    background-color: rgb(255, 251, 209);
    /* border: #0c004d; */
}

/* Shadow effect when scrolling */
.navbar.scrolled {
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1); /* Shadow on scroll */
}


.dropdown {
    position: relative;
    /* padding-right: 5%; */
}

.dropdown-content {
    display: none;
    position: absolute;
    /* top: 10px; */
    left: -100px;
    background-color: #f9f9f9;
    border-radius: 10px;
    min-width: 100px;
    box-shadow: 0px 8px 16px 0px rgba(0,0,0,0.2);
    z-index: 1;
}

.dropdown-content li {
    display: block;
}

.dropdown-content li a {
    /* color: rgb(68, 31, 89); */
    padding: 10px 0px;
    text-decoration: none;
    display: block;
    text-align: left; 
    font-size: 0.9rem;
}

.dropdown:hover .dropdown-content {
    display: block;
    cursor: pointer;
}
