.card {
    /* background-color: #4CAF50; */
    background-color: #4c66af;
    color: white;
    padding: 20px;
    margin: 10px 0;
    border-radius: 0 20px 20px 0;
    cursor: pointer;
    transition: transform 0.3s ease-in-out;
    overflow: hidden;
    position: relative;
    font-size: 0.9em;
}

.card.open {
    transform: translateX(10%);
    width: 75%;
    border-radius: 20px;
}

#current {
    background-color: #1e8f2d;
}

#prev {
    background-color: #ff7300;
}

#prev2 {
    background-color: #efb815;
}

.card-content {
    transition: opacity 0.3s ease-in-out;
}

.card.open .card-content {
    opacity: 1;
}

.card-content p {
    margin-top: 10px;
    opacity: 0;
}

.card.open .card-content p {
    opacity: 1;
}
